
import { ThreeJs } from './index'
import { Options, Vue } from 'vue-class-component';
import Loading from './Loading.vue'

@Options({
  components: {
    Loading
  }
})
export default class Demo extends Vue {
  selectedObject = ''
  status = 0
  mounted(): void {
    new ThreeJs(
      (name: string) => { this.selectedObject = name },
      (status: number) => { this.status = status } 
    )
  }
}
