
import { Options, Vue } from 'vue-class-component'
import Demo from './components/Demo.vue'
import Info from './components/Info.vue'

@Options({
  components: {
    Demo,
    Info
  },
})
export default class App extends Vue {
  showAside = 'animation-none'

  toggleAside() {
    if (this.showAside !== 'animation-show-aside') {
      this.showAside = 'animation-show-aside'
    } else {
      this.showAside = 'animation-hide-aside'
    }
  }
}
