import * as THREE from 'three'

import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { EXRLoader } from 'three/examples/jsm/loaders/EXRLoader'

import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer"
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass"
import { OutlinePass } from "three/examples/jsm/postprocessing/OutlinePass"
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass"
import { GUI } from 'three/examples/jsm/libs/lil-gui.module.min.js'
// import { FXAAShader } from "three/examples/jsm/shaders/FXAAShader"
import { GammaCorrectionShader } from 'three/examples/jsm/shaders/GammaCorrectionShader'

// 'three/examples/jsm/libs/lil-gui.module.min'

class Microscope {
  public baseObjects: Array<THREE.Object3D<THREE.Event> | null> = [ null, null, null ]
  public basePositions: Array<number> = [ 0, 0, 0 ]
  public groupX = new Array<THREE.Object3D<THREE.Event>>()
  public groupY = new Array<THREE.Object3D<THREE.Event>>()
  public groupZ = new Array<THREE.Object3D<THREE.Event>>()
}

export class ThreeJs {
  scene: THREE.Scene | null = null
  camera: THREE.PerspectiveCamera | null = null
  renderer: THREE.WebGLRenderer | null = null
  ambientLight: THREE.AmbientLight | null = null
  mesh: THREE.Mesh | null = null
  control: OrbitControls | null = null
  composer: EffectComposer | null = null
  outlinePass: OutlinePass | null = null
  envTexture: THREE.DataTexture | null = null
  selectObject: any
  updateStatus: any
  microscope = new Microscope
  config = {
    environment: true,
    background: false,
    orbitControls: 0,
    enableDamping: true,
    scaleDelta: 5
  }
  gui: GUI | null

  constructor(selectObject: any, updateStatus: any) {
    this.selectObject = selectObject
    this.updateStatus = updateStatus
    this.init();
  }

  init(): void {
    // 第一步新建一个场景
    this.scene = new THREE.Scene()

    this.setCamera()
    this.setHdr()
    this.setLight()
    this.setRenderer()
    this.setGltfModel()
    // this.setFbxModule()
    // this.setCube()
    this.outlineObj()

    this.animate()

    this.initEventMap()
  }

  initGui() {
    // https://lil-gui.georgealways.com/
    if (this.scene) {
      this.gui = new GUI
      this.gui.add(this.config, 'environment').onChange(
        (e: boolean) => {
          this.scene!.environment = e ? this.envTexture: null
        }
      ).name('环境光')
      this.gui.add(this.config, 'background').onChange(
        (e: boolean) => {
          this.scene!.background = e ? this.envTexture: null
        }
      ).name('背景图')
      this.gui.add(this.config, 'enableDamping').onChange(
        (e: boolean) => {
          if (this.control) {
            this.control.enableDamping = e
          }
        }
      ).name('阻尼效果')
      this.gui.add(this.config, 'orbitControls', { '缩放': 0, '滚动': 1}).onChange(
        (e: number) => {
          if (this.control) {
            this.control.enableZoom = e == 0
          }
        }
      ).name('鼠标滚轮')
      this.gui.add(this.config, 'scaleDelta', 0.5, 25, 0.5).name('缩放速度')
    }
  }

  initEventMap() {
    document.addEventListener('click', event => this.onPointerMove(event) )
    window.addEventListener('resize', () => this.onWindowResize())
    document.addEventListener('wheel', event => this.onMouseScroll(event), false)
    //其他浏览器直接绑定滚动事件
    // document.onmousewheel = document.onmousewheel = scrollFunc
    document.addEventListener('keypress', (event) => {
      // https://developer.mozilla.org/zh-CN/docs/Web/API/KeyboardEvent/key
      switch (event.key) {
        case 'z':
        case 'Z':
          if (this.camera && this.scene) {
            this.camera.fov += this.config.scaleDelta;
            this.camera.updateProjectionMatrix();
            this.renderer?.render(this.scene, this.camera);
          }
          break
        case 'x':
        case 'X':
          if (this.camera && this.scene) {
            this.camera.fov -= this.config.scaleDelta;
            this.camera.updateProjectionMatrix();
            this.renderer?.render(this.scene, this.camera);
          }
          break
      }
    })
  }

  // 新建透视相机
  setCamera(): void {
    // 第二参数就是 长度和宽度比 默认采用浏览器  返回以像素为单位的窗口的内部宽度和高度
    this.camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    this.camera.position.set(-2, 5, -5)
    // this.camera.rotation.set(72.6 * 180 / Math.PI, 0, -114 * 180 / Math.PI)
    this.camera.rotation.set(0, 0, Math.PI);
  }

  // 设置渲染器
  setRenderer(): void {
    const demo = document.getElementById('demoCanvas') as HTMLCanvasElement

    this.renderer = new THREE.WebGLRenderer({
      canvas: demo,
      antialias: true,     //抗锯齿
    });
    // 开启HiDPI设置
    this.renderer.setPixelRatio(window.devicePixelRatio)
    // 设置画布的大小
    this.renderer.setSize(window.innerWidth, window.innerHeight)
    this.renderer.shadowMap.enabled = true
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap

    this.renderer.toneMapping = THREE.ACESFilmicToneMapping
    this.renderer.toneMappingExposure = 1
    this.renderer.outputEncoding = THREE.sRGBEncoding

    if (this.camera) {
      this.control = new OrbitControls(this.camera, this.renderer.domElement)
      this.control.enableDamping = true
    }
  }

  setHdr(): void {
    if (this.scene) {
      new EXRLoader().load( './static/brown_photostudio_02_4k.exr', ( texture ) => {
        texture.mapping = THREE.EquirectangularReflectionMapping;

        this.envTexture = texture

        // torusMesh.material.envMap = pmremGenerator.fromEquirectangular( texture );
        this.scene!.environment = texture
      } );
    }
  }

  // 设置环境光
  setLight(): void {
    if (this.scene) {
      this.ambientLight = new THREE.AmbientLight(0xffffff) // 环境光
      this.ambientLight.position.set(0, 9, 9)
      this.ambientLight.intensity = 0.2
      this.scene.add(this.ambientLight)
      this.render()
    }
  }

  setGltfModel(): void {
    if (this.scene) {
      const gltfLoader = new GLTFLoader().setPath('./static/')
      // const gltfLoader = new GLTFLoader().setPath('/glTF/')
      gltfLoader.load(
        'test.gltf',
        // 'DamagedHelmet.gltf',
        (gltf) => {
          console.log('complete')

          gltf.scene.traverse((child: any) => {
            if (child.isMesh) {
              // child.frustumCulled = false
              child.castShadow = true
              child.receiveShadow = true

              switch (child.name) {
                // case '载物台X':
                case '载物台圆环':
                  this.microscope.groupX.push(child)
                  this.microscope.groupY.push(child)
                  break
                case '载物台Y':
                  this.microscope.baseObjects[1] = child
                  this.microscope.basePositions[1] = child.getWorldPosition(child.position).z
                  break
                case '手轮基座':
                case '手轮杆':
                case '纵动手轮':
                case '横动手轮':
                  this.microscope.groupY.push(child)
                  break
                case '镜台':
                  this.microscope.baseObjects[2] = child
                  this.microscope.basePositions[2] = child.getWorldPosition(child.position).y
                  break
                case '调节螺钉':
                case '接受光镜头':
                case '接收光镜片':
                case '转换器':
                case '左目镜基座':
                case '右目镜基座':
                case '目镜分割条':
                case '双筒目镜组合':
                case '目镜台':
                case '目镜镜片':
                case '目镜套':
                case '目镜套环':
                case '目镜旋钮':
                  this.microscope.groupZ.push(child)
                  break
                default:
                  break
              }
            } else if (child.isGroup && child.name === '载物台X') {
              this.microscope.baseObjects[0] = child
              this.microscope.basePositions[0] = child.getWorldPosition(child.position).x
              this.microscope.groupY.push(child)
            }
          })
          this.scene!.add(gltf.scene)
          this.render()
          this.updateStatus(1)
          this.initGui()
        },
        (progress) => {
          console.log(`${progress.loaded / progress.total * 100}% loaded`)
        },
        (error) => {
          console.log('faile to load', error)
          this.updateStatus(-1)
        },
      )
    }
  }

  // 创建网格模型
  setCube(): void {
    if (this.scene) {
      const geometry = new THREE.BoxGeometry() //创建一个立方体几何对象Geometry
      // const material = new THREE.MeshBasicMaterial({ color: 0xff3200 }); //材质对象Material
      // const texture = new THREE.TextureLoader().load(
      //   "/assets/imgs/dalishi.jpg"
      // ) //首先，获取到纹理
      const material = new THREE.MeshBasicMaterial({ 
        color: 0xff0000
       }) //然后创建一个phong材质来处理着色，并传递给纹理映射
      this.mesh = new THREE.Mesh(geometry, material) //网格模型对象Mesh
      this.scene.add(this.mesh) //网格模型添加到场景中
      this.render()
    }
  }

  private onPointerMove(event: MouseEvent): void {
    if (this.scene && this.camera && this.outlinePass) {
      const pointer = new THREE.Vector2()
      pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1;
      pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
  
      const raycaster = new THREE.Raycaster()
      raycaster.setFromCamera( pointer, this.camera );

      const intersects = raycaster.intersectObjects(this.scene.children, true);
      if (intersects && intersects.length > 0 ) {
        const obj = intersects[0].object
        // console.log(obj.name, obj.type)
        this.selectObject(obj.name)
        this.outlinePass.selectedObjects[0] = obj
      } else {
        this.selectObject('')
        this.outlinePass.selectedObjects = []
      }

    }
  }

  private onWindowResize() {
    const width = window.innerWidth;
    const height = window.innerHeight;

    if (this.camera) {
      this.camera.aspect = width / height;
      this.camera.updateProjectionMatrix();
    }

    this.renderer?.setSize( width, height );
    this.composer?.setSize( width, height );

    // effectFXAA.uniforms[ 'resolution' ].value.set( 1 / window.innerWidth, 1 / window.innerHeight );
  }

  private onMouseScroll(e: WheelEvent) {
    if (this.config.orbitControls == 1 && this.outlinePass?.selectedObjects.length) {
      const obj = this.outlinePass.selectedObjects[0]
      switch (obj.name) {
        case '粗动调焦手轮':
          obj.rotateOnWorldAxis(new THREE.Vector3(1, 0, 0), e.deltaY / (120 * Math.PI))
          this.moveZ(-e.deltaY / 15000.0)
          break
        case '微动调焦手轮':
          obj.rotateOnWorldAxis(new THREE.Vector3(1, 0, 0), e.deltaY / 400)
          this.moveZ(-e.deltaY / 150000.0)
          break
        case '转换器':
          obj.rotateY((e.deltaY > 0 ? -Math.PI : Math.PI) / 3)
          break
        case '纵动手轮':
          obj.rotateOnWorldAxis(new THREE.Vector3(0, 1, 0), e.deltaY / 400)
          this.moveY(-e.deltaY / 15000.0)
          break
        case '横动手轮':
          obj.rotateOnWorldAxis(new THREE.Vector3(0, 1, 0), e.deltaY / 400)
          this.moveX(-e.deltaY / 15000.0)
          break
        case '灯泡亮度旋钮':
          obj.rotateOnWorldAxis(new THREE.Vector3(-1, 0, 0), e.deltaY / 300)
          break
        default:
          break
      }
    }
  }

  private moveX(delta: number) {
    const obj = this.microscope.baseObjects[0]
    if (!obj) return

    const pos = obj.getWorldPosition(obj.position)
    const p = pos.x + delta
    // const p = this.microscope.basePositions[0] + delta
    if (p > -0.1 && p < 0.059) {
      pos.x = p

      for (const obj of this.microscope.groupX) {
        // const vec = new THREE.Vector3
        const pos = obj.getWorldPosition(obj.position)
        pos.x += delta
      }
    }
  }

  private moveY(delta: number) {
    const obj = this.microscope.baseObjects[1]
    if (!obj) return

    const pos = obj.getWorldPosition(obj.position)
    const p = pos.z + delta
    // const p = this.microscope.basePositions[1] + delta
    if (p > -0.587 && p < -0.178) {
      pos.z = p

      for (const obj of this.microscope.groupY) {
        // const vec = new THREE.Vector3
        const pos = obj.getWorldPosition(obj.position)
        pos.z += delta
      }
    }
  }

  private moveZ(delta: number) {
    const obj = this.microscope.baseObjects[2]
    if (!obj) return

    const pos = obj.getWorldPosition(obj.position)
    const p = pos.y + delta
    // const p = this.microscope.basePositions[2] + delta
    if (p < 0.147 && p > -0.233) {
      pos.y = p

      for (const obj of this.microscope.groupZ) {
        const pos = obj.getWorldPosition(obj.position)
        pos.y += delta
      }
    }

  }

  // 渲染
  render(): void {
    if (this.renderer && this.scene && this.camera) {
      this.renderer.render(this.scene, this.camera)
    }
  }

  outlineObj() {
    // 创建一个EffectComposer（效果组合器）对象，然后在该对象上添加后期处理通道。
    this.composer = new EffectComposer(this.renderer!)
    // 新建一个场景通道  为了覆盖到原理来的场景上
    const renderPass = new RenderPass(this.scene!, this.camera!)
    this.composer.addPass(renderPass)
    // 物体边缘发光通道
    this.outlinePass = new OutlinePass(new THREE.Vector2(window.innerWidth, window.innerHeight), this.scene!, this.camera!)
    this.outlinePass.edgeStrength = 5.0 // 边框的亮度
    this.outlinePass.edgeGlow = 1    // 光晕[0,1]
    this.outlinePass.edgeThickness = 1.0 // 边框宽度
    this.outlinePass.pulsePeriod = 5 // 呼吸闪烁的速度
    this.outlinePass.visibleEdgeColor.set(0xff0000) // 呼吸显示的颜色
    this.outlinePass.renderToScreen = true
    this.composer.addPass(this.outlinePass)

    // // 自定义的着色器通道 作为参数
    const gammaCorrectionPass = new ShaderPass( GammaCorrectionShader )
    this.composer.addPass(gammaCorrectionPass)
  }

  // 动画
  animate(): void {
    if (this.control) {
      this.control.update();
      requestAnimationFrame(this.animate.bind(this))
      this.render()
      if (this.outlinePass?.selectedObjects.length) {
        this.composer?.render()
      }
    }
  }
}
